// Types
interface SmartListFilter {
  field: string
  operator: 'equals' | 'not_equals' | 'contains' | 'not_contains' | 'greater_than' | 'less_than' | 'is_empty' | 'is_not_empty' | 'in' | 'not_in'
  value: any
}

interface SmartList {
  id: string
  name: string
  description?: string
  filters: SmartListFilter[]
  filterLogic: 'AND' | 'OR'
  contactCount: number
  isDefault: boolean
  color?: string
  icon?: string
  createdBy: string
  createdAt: string
  updatedAt: string
  lastUsedAt?: string
}

